//----------------
// Borders
// ----------------
$system-border: (
  border: (
    10: (
      'width': 1px,
      'style': solid,
      'color': 'gray-500'
    ),
    20: (
      'width': 1px,
      'style': solid,
      'color': 'gray-300'
    ),
    30: (
      'width': 1px,
      'style': solid,
      'color': 'gray-200'
    )
  )
);

/*
//----------------
// Font size
// ----------------
$system-font-size: (
  font-size: (
    '2xs': 1.3rem,
    'xs': 1.4rem,
    'sm': 1.6rem,
    'md': 2.0rem,
    'lg': 2.1rem,
    'xl': 2.5rem,
    '2xl': 3.1rem,
    '3xl': 3.9rem
  )
) !default;*/

//----------------
// Shadow
// ----------------
$system-shadow: (
  shadow: (
    'light': (
      'h-offset': 0,
      'v-offset': 2px, 
      'blur': 4px,
      'spread': 0,
      'color': $theme-color-shadow
      ),
    'medium': (
      'h-offset': 0,
      'v-offset': 4px, 
      'blur': 8px,
      'spread': 0,
      'color': $theme-color-shadow
    ),
    'heavy': (
      'h-offset': 0,
      'v-offset': 8px, 
      'blur': 16px,
      'spread': 0,
      'color': $theme-color-shadow)
  )
) !default;

$system-properties: map-merge($system-border, $system-shadow);

//----------------
// Grid
// ----------------

// Common grid breakpoints variables
$grid-xs:                     0 !default;
$grid-sm:                     576px !default;
$grid-md:                     768px !default;
$grid-lg:                     992px !default;
$grid-xl:                     1200px !default;

// Grid breakpoints..
$grid-breakpoints: (
  xs: $grid-xs,
  sm: $grid-sm,
  md: $grid-md,
  lg: $grid-lg,
  xl: $grid-xl
) !default;

// Define the maximum width of `.container` for different screen sizes.
$container-max-widths: (
  sm: 100%,
  md: 100%,
  lg: 100%,
  xl: 1200px
) !default;

$grid-columns:                  12 !default;
$grid-gutter-width:             32px !default; //16px on each side of the columns

// Spacing helpers
//
// Mostly used to control spacing.
// You can add more entries to the $spacers map, should you need more variation.
// How to use: units(2); This will give 4px (8*0.5).

$spacer: 8px !default;
$spacers: (
  0: 0,
  1: ($spacer * .25),   //2px
  2: ($spacer * .5),    //4px
  3: $spacer,           //8px
  305:($spacer * 1.5),  //12px
  4: ($spacer * 2),     //16px
  405: ($spacer * 2.5), //20px
  5: ($spacer * 3),     //24px
  505: ($spacer * 3.5), //28px
  6: ($spacer * 4),     //32px
  605: ($spacer * 4.5), //36px
  7: ($spacer * 5),     //40px
  705: ($spacer * 5.5), //44px
  8: ($spacer * 6),     //48px
  805: ($spacer * 6.5), //52px
  9: ($spacer * 7),     //56px
  905: ($spacer * 7.5), //60px
) !default;

